import { CqsItem } from "@rp/common/api-types"
import { usePanelConfig } from "@rp/react/tanstack-api/discovery-panel-config"
import { LazyImage } from "@rp/react/web/common/base-components"
import { IconEntity } from "@rp/react/web/icons"
import { Box, styled } from "@rp/react/web/mui/core"

type Props = {
  cqsItem: CqsItem | undefined
}

const Icon = styled(IconEntity)(({ theme }) => ({
  color: theme.palette.grey[200],
}))

export const CategoryIcon = ({ cqsItem }: Props) => {
  const { data: panelConfig } = usePanelConfig()
  const { category, name, metadata4 } = cqsItem || {}
  const panelCategory = panelConfig?.discoveryPanelCategories?.find(item => item.label === category)
  return metadata4 ? (
    <LazyImage
      src={metadata4}
      alt={name}
      width={20}
      height={20}
      loadingFallback={<Box sx={{ width: 20, height: 20 }} />}
      fallback={<Icon icon={panelCategory?.icon || ""} />}
    />
  ) : (
    <Icon icon={panelCategory?.icon || ""} />
  )
}
